import * as moment from 'moment';
export class EEGerCommons {

    static limit5Days() {
        let fechaActual = new Date();
        return moment(fechaActual).add(-5, 'day').format("YYYY-MM-DD");
    }

    static sum30Days(fecha) {
        return moment(new Date(fecha)).add(30, 'day').add(-3, 'hours').format("DD/MM/YYYY");
    }

    static formattedDate(fecha) {
        if (fecha) {
            console.log(moment(new Date(fecha)).add(-3, 'hours').format("DD/MM/YYYY"), fecha);
            return moment(new Date(fecha)).add(-3, 'hours').format("DD/MM/YYYY");
        } else return '';

    }

}


