import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class UsuarioProvider {

    constructor(private http: HttpClient) { }

    // GetUsuarioById(id) {
    //     return this.http.get(environment.url + 'usuario/' + id)
    //         .catch(this.handleError);
    // }

    GetUsuarios() {
        return this.http.get(environment.url + 'usuario')
            .catch(this.handleError);
    }

    PostUsuario(usuarioForm) {
        return this.http.post(environment.url + 'usuario', usuarioForm)
            .catch(this.handleError);
    }

    PutUsuario(usuarioForm) {
        return this.http.put(environment.url + 'usuario', usuarioForm)
            .catch(this.handleError);
    }

    DeleteUsuario(id) {
        return this.http.delete(environment.url + 'usuario/' + id)
            .catch(this.handleError);
    }

    GetUsuarioDetalle(username) {
        return this.http.get(environment.url + 'personal/detalle/' + username)
            .catch(this.handleError);
    }

    handleError(error: Response) {
        return Observable.throw("error");
    }
}


