import { Component, OnInit } from '@angular/core';
import { AuthenticationProvider } from './../../../providers/authentication.provider';
import { EELoading } from './../../../providers/ee-loading-provider';

@Component({
    selector: 'my-dashboard',
    template: require('./dashboard.component.html')
})
export class DashboardComponent {
    pageProps;
    user;
    private ocupacion;
    constructor( private systemProvider: AuthenticationProvider,
                 private eeloading: EELoading) {

    }

    getMonData = () => {
        const data = [];
        for (let i = 0; i < 13; i++) {
            data.push('Mon. ' + i);
        }
        return data;
    }

    monData = this.getMonData();

    ngOnInit() {
        this.getOcupacion();
    }

    getOcupacion() {
        this.ocupacion = [];
        this.systemProvider
            .getOcupacion()
            .subscribe(
                (data: any) => {
                this.eeloading.close();
                this.ocupacion = data;
                },
                error => {
                    this.eeloading.close();
                }
        )
    }

}



