import { environment } from './../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { HttpClient } from "@angular/common/http";

@Injectable()
export class PersonalResidenciaProvider {

    constructor(private http: HttpClient) { }

    PostPersonalResidencia(personalResidenciaForm) {
        return this.http.post(environment.url + 'personalResidencia', personalResidenciaForm)
            .catch(this.handleError);
    }

    PutPersonalResidencia(personalResidenciaForm) {
        return this.http.put(environment.url + 'personalResidencia', personalResidenciaForm)
            .catch(this.handleError);
    }

    DeletePersonalResidencia(id) {
        return this.http.delete(environment.url + 'personalResidencia/' + id)
            .catch(this.handleError);
    }

    GetPersonalResidenciaDetalle(id, userid) {
        return this.http.get(environment.url + 'personalResidencia/detalle/' + id + '?userid=' + userid)
            .catch(this.handleError);
    }

    handleError(error: Response) {
        return Observable.throw("error");
    }
}


